const mapsheetData=(sheetData) => {
  if (!Array.isArray(sheetData) || sheetData.length === 0) {
    return { items: [] };
  }

  const items = sheetData.map(row => ({
    type: row['Physical product/Raw Material']?.toLowerCase() === 'physical product' 
      ? 'VPMReference' 
      : 'Raw_Material',
    title: row['Title'] || '',
    attributes: {
      description: row['Description'] || 'Request is coming from Excel upload',
      "dseno:EnterpriseAttributes": {
        "XP_VPMReference_Ext.EMR_ERP_PrimaryUOM": row['Unit of Measure'] || 'Each'
      },
      "dseng:EnterpriseReference": {
        "partNumber": row['EIN Number'] || ''
      }
    },
    classificationType: row['Type'] || 'Electronics',
    collabspace: row['Collabspace'] || ''
  }));

  return { items };
};

export default mapsheetData;